* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.mainContainer {
    margin: 150px auto;
    width: 100%;

    display: flex;
}

.navSideRoutes {
    width: 200px;


    margin: 50px 0 0 15px;
}

.blockForm {
    display: flex;
    flex-direction: column;
}

.userDataContainer {
    width: 70%;
    height: 430px;

    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    margin-left: 100px;
}

.inputParms {
    width: 50%;
    height: 35px;
    margin: 10px auto;
    border-radius: 5px;
    border: 2px solid silver;
}

.birthdayInput {
    width: 20%;
    height: 35px;
    margin: 10px auto 0;
}

.nameUserContainer {
    width: 56%;
    margin: 0 auto;
    display: flex;
    justify-content: space-around;
}

.inputNameParams {
    width: 40%;
    height: 35px;
    border-radius: 5px;
    margin: 10px auto;
    border: 2px solid silver;
}

.adressContainer {
    width: 50%;
    margin: 0 auto 0px;

}

.navSideRoutes li {
    list-style: none;
    width: 100%;
    height: 70px;
    border: 2px solid silver;
    border-radius: 5px;
    display: flex;
    padding-top: 20px;
    margin: 10px auto 20px;
    justify-content: center;
}

.navSideRoutes li:hover {
    background-color: #b5d8f7;
    border-color: #6aa2d3;
}

.linkInCabinet {
    text-decoration: none;
    font-size: 18px;
    color: #6aa2d3;
    font-weight: bold;
}

.linkInCabinet:hover {
    color: aliceblue;
}

.saveButtonsGroup {
    width: 100%;

    margin: 50px auto;
    display: flex;
    margin: 40px auto 100px;
    justify-content: space-around;
}

.buttonsParamsStyle {
    width: 220px;
    height: 55px;
    background-color: #b5d8f7;
    border: none;
    border-radius: 7px;
    font-family: 'Times New Roman', Times, serif;
    font-size: 24px;
    font-weight: bold;
}

.buttonsParamsStyle:hover {
    cursor: pointer;
    border: 2px solid rgb(47, 120, 255);
    background-color: #6aa2d3;
    color: aliceblue;
    text-decoration: underline;
}

.adressListBlock {
    width: 80%;
    margin: 0 auto;
    border: 2px solid grey;
    border-radius: 7px;
    padding-top: 15px;
}

.adressListBlock li {
    list-style: none;
    height: 55px;
    display: flex;
    justify-content: center;
}

.mainContainerPass {

    width: 100%;
}

.passContainer {
    width: 60%;
    margin: 15px auto;

    border: 3px solid #b5d8f7;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.ChangePassForm {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.userDataContainer {
    width: 80%;
    margin: 15px auto;

    display: flex;
    flex-direction: column;
    justify-content: center;
}

.changeNameBlock {
    margin-top: 150px;
}

.closeImg {
    width: 25px;
    height: 25px;
    position: absolute;
    top: 2px;
    right: 2px;
}

.cabinetPopUp {
    width: 400px;
    height: 120px;
    background-color: aliceblue;
    border: 1px solid black;
    border-radius: 5px;
    position: relative;

}

.popUpText {
    color: black;
    position: absolute;
    top: 50px;
    left: 35%;
}

.inputAdres {
    height: 45px;
    width: 90%;
}

@media screen and (min-width: 768px) and (max-width: 1024px) {

    .buttonsParamsStyle {
        width: 40%;

        font-size: 18px;
    }

}

@media screen and (min-width: 320px) and (max-width: 768px) {

    .changeNameBlock,
    .mainContainer {
        width: 80vh;
        display: flex;
        justify-content: center;
        /* border: 1px solid black; */
        margin: 120px auto 0;
    }



    .inputNameParams {
        width: 40%;
    }

    .nameUserContainer {
        width: 99%;
        display: flex;
        /* border: 1px solid black; */
        margin: 10px auto 0;
    }

    .inputAdres {
        height: 35px;
        width: 90%;
    }

    .adressListBlock {
        width: 80%;
        margin: 0 auto;
        height: 20%;
        border: 2px solid grey;
        border-radius: 7px;
        padding-top: 15px;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .saveButtonsGroup {
        width: 100%;

        margin: 50px auto;
        display: flex;
        margin: 40px auto 100px;
        justify-content: space-between;
    }

    .buttonsParamsStyle {
        width: 45%;
        height: 55px;

        background-color: #b5d8f7;
        border: none;
        border-radius: 7px;
        font-family: 'Times New Roman', Times, serif;
        font-size: 12px;
        font-weight: bold;
    }

    .linkInCabinet {
        text-decoration: none;
        font-size: 14px;
        color: #6aa2d3;
        font-weight: bold;
    }

    .navList {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }







}